<template>
    <v-container style="width: 98%;" align="center" fluid>
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <v-col class="text-center">
                    <v-toolbar-title>{{rankingText}}</v-toolbar-title>
                </v-col>
            </v-row>
        </v-app-bar>

        <v-row v-if="users.length > 0" class="text-center justify-center" dense
               style="margin-top: 64px; padding: 0px 1% 80px 1%">
            <div
                    style="width: 100%; padding-bottom: 10px; max-width: 1200px"
                    v-for="(user,index) in users"
                    :key="user.id"
            >
                <v-card
                        v-if="user.id != myUserID"
                        style="padding: 2px"
                        height="16vh"
                        max-height="150px"
                        min-height="114px"
                        max-width="1200px"
                        @click="$router.push({path: '/dashboard/search/user/' + user.id})">
                    <v-card-actions class="pa-0 ma-0 justify-center" style="width: 100%; height: 100%">
                        <v-row class="pa-0 " style="height: 100%; vertical-align: center; max-width: 1200px"
                               align-self="center">
                            <v-col class="text-center" style="height: 100%; vertical-align: center; position: relative"
                                   align-self="center">
                                <img v-if="index == 0" src="img/medals/first.svg" class="centered-medal">
                                <img v-else-if="index == 1" src="img/medals/second.svg" class="centered-medal">
                                <img v-else-if="index == 2" src="img/medals/third.svg" class="centered-medal">
                                <h2 v-else class="primary--text vertical-align-center centered-medal"
                                    style="font-size: 4vh; max-font-size: 50px;">{{index+1}}</h2>
                            </v-col>
                            <v-col class="text-center secondary--text vertical-align: center" style="position: relative"
                                   align-self="center">
                                <div class="centered-img-text">
                                    <v-avatar
                                            style="width: 46%; padding-top: 46%; border-radius: 50%; min-height: 62px; min-width: 62px; max-height: 100%">
                                        <img
                                                style="width: 100%; position: absolute; top: 0; height: 100%; object-position: center; object-fit: cover;"
                                                class="white--text align-end"
                                                :aspect-ratio="1"
                                                :src="user.profileImg"
                                        >
                                    </v-avatar>
                                    <p><strong style="font-size: 14px">@{{user.username}}</strong></p>
                                </div>
                            </v-col>

                            <v-col class="text-center secondary--text vertical-align: center" style="position: relative"
                                   align-self="center">
                                <div class="centered-img-text">
                                    <p style="font-size: 18px">
                                        <v-icon color="yellow darken-3" large>mdi-star</v-icon>
                                        <br>
                                        {{user.score}}
                                    </p>
                                </div>
                            </v-col>
                        </v-row>
                    </v-card-actions>
                </v-card>
                <v-card
                        v-if="user.id == myUserID"
                        dark
                        color="primary"
                        style="padding: 2px"
                        height="16vh"
                        max-height="160px"
                        min-height="114px"
                        max-width="1200px"
                        @click="$router.push({path: '/dashboard/search/user/' + user.id})">
                    <v-card-actions class="pa-0 ma-0 justify-center" style="width: 100%; height: 100%">
                        <v-row class="pa-0 " style="height: 100%; vertical-align: center; max-width: 1200px"
                               align-self="center">
                            <v-col class="text-center" style="height: 100%; vertical-align: center; position: relative"
                                   align-self="center">
                                <img v-if="index == 0" src="img/medals/first.svg" class="centered-medal">
                                <img v-else-if="index == 1" src="img/medals/second.svg" class="centered-medal">
                                <img v-else-if="index == 2" src="img/medals/third.svg" class="centered-medal">
                                <h2 v-else class="white--text vertical-align-center centered-medal"
                                    style="font-size: 4vh; max-font-size: 50px;">{{index+1}}</h2>
                            </v-col>
                            <v-col class="text-center white--text vertical-align: center" style="position: relative"
                                   align-self="center">
                                <div class="centered-img-text">
                                    <v-avatar
                                            style="width: 46%; padding-top: 46%; border-radius: 50%; min-height: 62px; min-width: 62px; max-height: 100%">
                                        <img
                                                style="width: 100%; position: absolute; top: 0; height: 100%; object-position: center; object-fit: cover;"
                                                class="white--text align-end"
                                                :aspect-ratio="1"
                                                :src="user.profileImg"
                                        >
                                    </v-avatar>
                                    <p><strong style="font-size: 14px">@{{user.username}}</strong></p>
                                </div>
                            </v-col>

                            <v-col class="text-center white--text vertical-align: center" style="position: relative"
                                   align-self="center">
                                <div class="centered-img-text">
                                    <p style="font-size: 18px">
                                        <v-icon color="yellow darken-3" large>mdi-star</v-icon>
                                        <br>
                                        {{user.score}}
                                    </p>
                                </div>
                            </v-col>
                        </v-row>
                    </v-card-actions>
                </v-card>
            </div>
            <v-progress-circular
                    v-if="pageLoading"
                    indeterminate
                    color="primary"
                    style="margin-bottom: 50px"
            ></v-progress-circular>
        </v-row>

        <v-btn
                v-scroll="onScroll"
                style="z-index: 3; bottom: 80px"
                v-show="fabScroll && users.length > 0"
                fab
                dark
                fixed
                right
                color="primary"
                @click="toTop"
        >
            <v-icon>mdi-arrow-up</v-icon>
        </v-btn>
        <v-row v-if="users.length == 0">
            <p>{{problemRetrievingRankingText}}</p>
        </v-row>
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {mapActions, mapGetters} from 'vuex';

    // Página de ranking de usuario de la aplicación
    // @group General
    export default {
        name: "RankingHome",
        data() {
            return {
                problemRetrievingRankingText: null,
                rankingText: null,
                snackbarText: null,
                snackbar: false,
                closeText: null,
                buttonIsDisabled: false,
                myUserID: this.$store.getters['user/getID'],
                pageLoading: false,
                fabScroll: false
            }
        },
        methods: {
            ...mapActions('ranking', ['getPagedRankingUsers', 'areThereMoreRankingUsersToShow']),

            // @vuese
            // Detecta cuando se ha realizado scroll en la página
            onScroll(e) {
                if (typeof window === 'undefined') return
                const top = window.pageYOffset || e.target.scrollTop || 0
                this.fabScroll = top > 20
            },

            // @vuese
            // Realiza un scroll automático al inicio de la página
            toTop() {
                this.$vuetify.goTo(0)
            },

            // @vuese
            // Obtiene los usuarios a presentar en el ranking de forma paginada
            listRankingUsers: function () {
                let self = this;
                self.buttonIsDisabled = true;
                let resultGetPagedRankingUsers = self.getPagedRankingUsers();

                return resultGetPagedRankingUsers.then((moreUsersToShow) => {
                    self.buttonIsDisabled = !moreUsersToShow;
                    self.pageLoading = false;
                })
                    .catch((error) => {
                        self.pageLoading = false;
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            }
        },
        created: async function () {
            this.problemRetrievingRankingText = this.$i18n.t('messages.ERROR_PROBLEM_RETRIEVING_RANKING');
            this.rankingText = this.$i18n.t('messages.TEXT_RANKING');


            let self = this;
            let $ = require('jquery');

            if (self.users.length == 0) {
                let available = true;
                self.buttonIsDisabled = true;

                let first = true;

                while (first || (available && $(document).height() <= window.screen.height)) {
                    first = false;
                    await self.getPagedRankingUsers().then((moreUsersToShow) => {
                        available = moreUsersToShow;
                        self.pageLoading = false;
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                            self.pageLoading = false;
                        });
                }
                self.buttonIsDisabled = !available;

            } else {
                self.areThereMoreRankingUsersToShow().then((moreUsersToShow) => {
                    self.buttonIsDisabled = !moreUsersToShow;
                });
            }
        },
        mounted() {
            let self = this;
            let $ = require('jquery');

            $(window).scroll(function () {
                let bottom_position = $(document).height() - ($(window).scrollTop() + window.screen.height);

                if (!self.buttonIsDisabled && bottom_position < 1) {
                    self.pageLoading = true;
                    self.listRankingUsers();
                }
            });
        },
        computed: {
            ...mapGetters('ranking', {users: 'getRankingUsersToShow'})
        }
    }
</script>

<style scoped>
    .centered-medal {
        max-width: 100%;
        max-height: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
    }

    .centered-img-text {
        position: absolute;
        top: 50%;
        left: 50%;
        -ms-transform: translate(-50%, -40%);
        transform: translate(-50%, -40%);
    }
</style>